@font-face {
  font-family: 'Perpetua';
  src: url(/static/media/Perpetua.8dc8894f.eot);
  src: url(/static/media/Perpetua.66b85eeb.woff) format('woff'),
  url(/static/media/Perpetua.93133519.woff2) format('opentype'),
  url(/static/media/Perpetua.f0d9afc1.svg) format('svg');
}

:root{
  --app-height: 100%;
}

* {
  margin: 0;
  padding: 0;
  font-family: 'Perpetua';
  -webkit-font-feature-settings: normal;
          font-feature-settings: normal;
  font-variant: normal;
  font-style: normal;
}

html{
  --primary-black: #2d2d2d;
  --secondary-black: #565656;
  --primary-white: #fff;
  --secondary-white: #e1e1e1;
  --title-color: #D9AF6A;
  overflow-y: scroll;
  overflow-x: hidden;
}



.footer-container{
    width: 100vw;
    padding: 18px 0;
    background-color: black;
    color: white;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.fai-insta{
    width: 28px;
    height: 28px;
    color: white;
    margin: 0 1vw 0 2vw;
    cursor: pointer;
}

.copyrights{
    font-size: 18px;
    margin: 0 2vw 0 1vw;
}

/* smaller screens */
@media screen and (max-width: 1200px) {
    .footer-container{
        padding: 15px 0;
    }
    
    .fai-fb{
        width: 26px;
        height: 26px;
    }
    
    .copyrights{
        font-size: 17px;
        margin: 0 2vw 0 1vw;
    }
}

/* tablets and smartphones */
@media screen and (max-width: 768px){
    .footer-container{
        padding: 12px 0;
    }
    
    .fai-fb{
        width: 26px;
        height: 26px;
    }
    
    .copyrights{
        font-size: 14.5px;
        margin: 0 2vw 0 1vw;
    }


}
.section-contact{
    width: 100vw;
    height: auto;
    display: flex;
    flex-direction: row;
}

.section-contact-image-tile{
    background-image: url(/static/media/showroom4.87b40d78.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    width: 50vw;
    display: flex;
}

.section-contact-tile{
    background-color: var(--primary-black);
    width: 50vw;
    min-height: 40vw;
    height: auto;
    display: flex;
    flex-direction: column;
    padding: 40px 0;
}

.section-contact-wrapper{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    /* background-color: tomato; */
}

.profile-pic{
    width: 120px;
    height: 120px;
    margin-right: 20px;
    border-radius: 50%;
    background-color: khaki;
    background-image: url(/static/media/portrait.45d79079.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

.contact-info-column{
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: 30px;
    color: var(--primary-white);
}

.contact-info-tel{
    font-style: italic;
}

.location-wrapper{
    /* background-color: blueviolet; */
    padding: 60px 0 20px 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    color: white;
    font-size: 28px;
}

.location-map{
    width: 30vw;
    height: 16vw;
    margin-top: 12px;
}

/* smaller screens */
@media screen and (max-width: 1200px) {
    
    .profile-pic{
        width: 100px;
        height: 100px;
        margin-right: 18px;
    }

    .contact-info-column{
        font-size: 27px;
    }

    .location-wrapper{
        padding: 54px 0 10px 0;
        font-size: 26px;
    }

}

/* tablets and smartphones */
/* portrait orientation */
@media screen and (max-width: 768px){

    .section-contact{
        flex-direction: column;
    }

    .section-contact-image-tile{
        width: 100vw;
        height: 180px;
    }

    .section-contact-tile{
        width: 100vw;
        padding: 34px 0;
        min-height: 80vw;
    }

    
    .profile-pic{
        width: 80px;
        height: 80px;
        margin-right: 15px;
    }

    .contact-info-column{
        font-size: 25px;
    }

    .location-wrapper{
        padding: 48px 0 10px 0;
        font-size: 23px;
    }
    
    .location-map{
        width: 74vw;
        height: 40vw;
        margin-top: 12px;
    }
}


.navbar-button-mobile{
    background-color: var(--title-color);
    padding: 1.3vw 6.4vw;
    border-radius: 1.8vw;
    outline: none;
    border: none;
    cursor: pointer;
    font-size: 7.6vw;
    margin: 3.8vw 0;
    color: black;
}

.navbar-button-mobile:first-child{
    margin-top: 0;
}

/* landscape orientation */
@media screen and (orientation: landscape){
    .navbar-button-mobile{
        background-color: var(--title-color);
        padding: 0.6vw 3.6vw;
        font-size: 4.2vw;
        margin: 1.6vw 0;
        border-radius: 0.9vw;
    }
}


.navbar{
    justify-content: flex-end;
    align-items: center;
    position: absolute;
    top: 56px; right: 5.8vw;
    z-index: 999;
    /* background-color: royalblue; */
}

.nav-item{
    display: inline-block;
    margin: 0 26px;
    font-size: 31px;
    border-bottom: 2px solid var(--title-color);
    padding-bottom: 3px;
    color: var(--title-color);
    text-transform: uppercase;
    cursor: pointer;
}


.navbar-mobile{
    position: absolute;
    bottom: 0;
    width: 100vw;
    height: 50%;
    display: none;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}



/* smaller screens */
@media screen and (max-width: 1200px) {

    .navbar{
        position: absolute;
        top: 44px; right: 5.8vw;
    }

    .nav-item{
        margin: 0 18px;
        font-size: 24px;
        padding-bottom: 2.6px;
    }

}

/* tablets and smartphones */
/* portrait orientation */
@media screen and (max-width: 768px) and (orientation: portrait){

    .navbar{
        display: none;
    }

    .navbar-mobile{
        display: flex;
    }

}

/* landscape orientation */
@media screen and (max-width: 768px) and (orientation: landscape){

    .navbar{
        display: none;
    }

    .navbar-mobile{
        position: absolute;
        top: -30px; right: 20px;
        width: 50vw;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

}


.opening-hours-container{
    width: 100vw;
    padding: 26px 0;
    background-color: var(--primary-white);
    position: absolute;
    bottom: 0; left: 0;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.opening-hours-title{
    padding: 4px 10px;
    font-size: 42px;
    color: var(--title-color);
    /* background-color: tomato; */
    text-overflow: clip;
    vertical-align: middle;
    margin-left: 4vw;
    text-transform: uppercase;
}

.opening-hours-wrapper{
    width: auto;
    display: flex;
    flex: 1 1;
    justify-content: center;
    flex-direction: row;
    padding: 0 10px;
    /* background-color: violet; */
}

.opening-hours-day-wrapper{
    width: auto;
    display: flex;
    /* background-color: yellowgreen; */
    padding: 12px 2vw;
    border-right: 1px solid #e6e6e6;
    margin: 4px;
    align-items: center;
    flex-direction: column;
}

.opening-hours-day-wrapper:last-child{
    border-right: none;
}


.opening-hours-day{
    font-size: 24px;
    padding: 2px 0 8px 0;
    display: flex;
    justify-content: center;
    /* background-color: red; */
    color: var(--primary-black);
}

.opening-hours-hours{
    font-size: 20px;
    padding: 0 4px;
    display: flex;
    justify-content: center;
    /* background-color: yellow; */
    color: var(--secondary-black);
}

/* Mobile */

.opening-hours-mobile-modal{
    width: 100vw;
    padding: 12px 0;
    background-color: var(--primary-white);
    position: absolute;
    bottom: 0; left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    display: none;
    cursor: pointer;
}

.opening-hours-mobile-title{
    font-size: 8vw;
    color: var(--title-color);
    text-transform: uppercase;
    margin-left: 9vw;
}

.modal-sheet-header{
    width: 94vw;
    padding: 3vw 3vw;
    margin: 2vw 0;
    background-color: var(--primary-white);
    text-transform: uppercase;
    font-size: 8vw;
    color: var(--title-color);
    text-align: center;

}

.modal-sheet-content-wrapper{
    width: 86%;
    padding: 20px 7%;
    /* background-color: wheat; */
    color: var(--primary-white);
    display: flex;
    flex-direction: column;
    align-items: center;
}

.modal-sheet-content-table{
    width: 100%;
    border-collapse: collapse;
}

.modal-sheet-content-row{
    font-size: 22px;
    line-height: 22px;
    color: var(--primary-black);
}

.day-cell{
    /* background-color: saddlebrown; */
    vertical-align: middle;
    border-bottom: 1px solid #dbdbdb;
    padding: 12px 5px;
}

.ohours-cell{
    /* background-color: salmon; */
    font-style: italic;
    white-space: nowrap;
    vertical-align: middle;
    border-bottom: 1px solid #dbdbdb;
    padding: 12px 5px;
    text-align: end;
    /* column-span: ; */
}

.no-border{
    border: 0;
}

.no-pad-top{
    padding-top: 0;
}
.no-pad-bottom{
    padding-bottom: 0;
}

.icon-ri-arrow-up{
    margin-bottom: -3vw;
    margin-left: 1vw;
    width: 10vw;
    height: 10vw;
    color: #000;
}

.icon-ri-arrow-down{
    position: absolute;
    bottom: 20px;
    width: 12vw;
    height: 12vw;
    color: #000;
}


/* smaller screens */
@media screen and (max-width: 1200px) {

    .opening-hours-container{
        padding: 20px 0;
    }

    .opening-hours-title{
        font-size: 3vw;
        line-height: 3.1vw;
    }   
    
    .opening-hours-day-wrapper{
        padding: 8px 2vw;
    }

    .opening-hours-day{
        font-size: 2.05vw;
        padding: 2px 0 5px 0;
    }

    .opening-hours-hours{
        font-size: 1.75vw;
        padding: 0 4px;
    }

}


/* tablets and smartphones */
@media screen and (max-width: 768px){

    .opening-hours-container{
        display: none;
    }

    .opening-hours-mobile-modal{
        display: flex;
    }

}
/* landscape orientation */
@media screen and (max-width: 768px) and (orientation: landscape){

    .opening-hours-mobile-title{
        font-size: 5vw;
        margin-left: 7vw;
    }

    .icon-ri-arrow-up{
        margin-bottom: -2vw;
        margin-left: 0.7vw;
        width: 6.8vw;
        height: 6.8vw;
        color: #000;
    }

}

.hero-container{
    background-image: url(/static/media/cover_pc_cut_compressed.c8675011.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    object-fit: contain;
}

.hero-container-overlay{
    width: 100%;
    height: 100%;
    display: block;
    background-color: rgba(0, 0, 0, 0.42);
}

.hero-container-title{
    position: absolute;
    top: 60px; left: 5.8vw;
    display: flex;
    color: var(--primary-white);
    font-size: 5.3vw;
    line-height: 5.3vw;
    text-transform: uppercase;
    font-family: "Perpetua Titling MT", serif;
    /* background-color: rosybrown; */
}


/* smaller screens */
@media screen and (max-width: 1200px) {

    .hero-container-title{
        position: absolute;
        top: 44px; left: 5.8vw;
    }

}

/* tablets and smartphones */
/* portrait orientation */
@media screen and (max-width: 768px) and (orientation: portrait){

    .hero-container{
        background-image: url(/static/media/cover_mobile_compressed.4fe29187.jpg);
    }

    .hero-container-title{
        position: absolute;
        top: 0; left: 0;
        width: 100vw;
        height: 50%;
        display: flex;
        justify-content: center;
        flex-direction: column;
        text-align: center;
        font-size: 13vw;
        line-height: 13vw;
    }

}
/* landscape orientation */
@media screen and (max-width: 768px) and (orientation: landscape){

    .hero-container{
        background-image: url(/static/media/cover_mobile_compressed.4fe29187.jpg);
    }

    .hero-container-title{
        top: -30px; left: 20px;
        width: 50vw;
        height: 100%;
        display: flex;
        justify-content: center;
        flex-direction: column;
        font-size: 6.6vw;
        line-height: 6.6vw;
    }

}

.section-prices{
    width: 100vw;
    height: auto;
    display: flex;
    flex-direction: row;
}

.section-prices-image-tile{
    background-image: url(/static/media/showroom1.1333435a.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    width: 50vw;
    display: flex;
}

.section-prices-tile{
    background-color: var(--primary-black);
    width: 50vw;
    min-height: 40vw;
    height: auto;
    display: flex;
    flex-direction: column;
    padding: 40px 0;
}

.section-title{
    width: 100%;
    /* background-color: darkcyan; */
    display: flex;
    justify-content: center;
    flex-direction: row;
    align-items: center;
    text-align: center;
    font-size: 45px;
    color: var(--title-color);
    text-transform: uppercase;
    margin-bottom: 30px;
}

.section-prices-container{
    width: 100%;
    display: flex;
    flex-direction: column;
    /* background-color: tomato; */
}

.section-prices-selection-container{
    width: 90%;
    padding: 0 5%;
    /* background-color: thistle; */
    display: flex;
    flex-direction: row;
    justify-content: center;
    font-size: 25px;
    margin-bottom: 20px;
}

.cat-button{
    color: var(--primary-white);
    background-color: transparent;
    outline: none;
    border: none;
    cursor: pointer;
    margin-right: 1.2vw;
    font-size: 24px;
    padding: 4px 12px;
    border-radius: 8px;
}

.cat-button:last-child{
    margin: 0;
}

.cat-button.active{
    background-color: rgba(217, 175, 106, 0.2);
}


.section-prices-wrapper{
    width: 86%;
    padding: 20px 7%;
    /* background-color: wheat; */
    display: table;
    color: var(--primary-white);
}

.price-row{
    display: table-row;
    font-size: 22px;
    line-height: 22px;
}

.description{
    display: table-cell;
    /* background-color: saddlebrown; */
    vertical-align: middle;
    border-bottom: 1px solid #3B3B3B;
    padding: 8px 5px;
}


.price{
    display: table-cell;
    /* background-color: salmon; */
    font-style: italic;
    white-space: nowrap;
    vertical-align: middle;
    border-bottom: 1px solid #3B3B3B;
    padding: 8px 5px;
}

.price-row:last-child > .price, .price-row:last-child > .description{
    border: none;
}


/* smaller screens */
@media screen and (max-width: 1200px) {

    .section-title{
        font-size: 40.6px;
        margin-bottom: 18px;
    }

    .section-prices-selection-container{
        margin-bottom: 18px;
    }

    .cat-button{
        margin-right: 1vw;
        font-size: 22.8px;
        padding: 3.8px 10px;
        border-radius: 7px;
    }

    .price-row{
        font-size: 20px;
        line-height: 20px;
    }

}

/* tablets and smartphones */
/* portrait orientation */
@media screen and (max-width: 768px){
    
    .section-prices{
        width: 100vw;
        flex-direction: column-reverse;
    }
    
    .section-prices-image-tile{
        width: 100vw;
        height: 190px;
    }

    .section-prices-tile{
        width: 100vw;
        padding: 34px 0;
        min-height: 80vw;
    }

    .section-title{
        font-size: 8.4vw;
        margin-bottom: 14px;
    }
    
    .section-prices-selection-container{
        margin-bottom: 12px;
    }

    .cat-button{
        margin-right: 1vw;
        font-size: 5.2vw;
        padding: 3.8px 10px;
        border-radius: 6px;
    }

    .price-row{
        font-size: 4.3vw;
        line-height: 4vw;
    }
    
    .description{
        padding: 7.5px 5px;
    }
    
    
    .price{
        padding: 7.5px 5px;
    }

}

/* landscape orientation */
@media screen and (max-width: 768px) and (orientation: landscape){

    .section-prices-image-tile{
        height: 120px;
    }

    .section-prices-tile{
        min-height: 46vw;
    }

    .section-title{
        font-size: 5vw;
        margin-bottom: 12px;
    }

    .cat-button{
        margin-right: 1vw;
        font-size: 3vw;
        padding: 4px 10px;
    }

    .price-row{
        font-size: 2.4vw;
        line-height: 2.4vw;
    }
    
    .description{
        padding: 7.5px 5px;
    }
    
    .price{
        padding: 7.5px 5px;
    }

}



